import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, of } from 'rxjs';
import { catchError, tap } from 'rxjs/operators';
// import { SpinnerService } from '../shared/spinner/spinner.service';
// import {ApiConfig} from "./api-config/apiConfig"

@Injectable({
  providedIn: 'root'
})
export class ImgurHttpService {

  constructor(private http: HttpClient,
    // private spinner: SpinnerService 
  ) { }



  IMGUR_URL = "https://api.imgur.com/3/image";
  imgUrOptions = new HttpHeaders({
    // 'content-type': 'multipart/form-data',
    "Authorization": "Client-ID 58312da6cce3fa0"
    
  })

  public postImgur<Response>(body): Observable<any>{
    // this.showLoadingIcon();
    return this.http.post<Response>(this.IMGUR_URL, body, {headers: this.imgUrOptions}).pipe(
      tap((res)=> {
        console.log("imgur success http")
      }),
      catchError(this.handleError())
    );
  }

  public post<Response>(body){
    // this.showLoadingIcon();
    return this.http.post<Response>(this.IMGUR_URL, body, {headers: this.imgUrOptions}).pipe(
      tap((res)=> {
        console.log("imgur success http")
      }),
      catchError(this.handleError())
    );
  }

  
private handleError<T>(operation = 'operation', result?: T) {
  return (error: any): Observable<T> => {

    // TODO: send the error to remote logging infrastructure
    // console.error(error); // log to console instead

    // TODO: better job of transforming error for user consumption
    // this.log(`${operation} failed: ${error.message}`);

    // Let the app keep running by returning an empty result.
    return of(result as T);
  };
}








}
