import { Injectable } from '@angular/core';
import { Observable, Subject } from 'rxjs';
import { DataService } from '../data.service';
import { HttpServiceService } from '../http-service.service';
import { TokenService } from './token.service';
 

@Injectable({
  providedIn: 'root'
})
export class AuthService { 
  storeToken = new Subject<any>();
  onStoreToken = this.storeToken.asObservable(); 
  userData: {};

  isLoggedIn = new Subject<any>();
  onloggedIn = this.isLoggedIn.asObservable();

  isLogOut$ = new Subject<any>();
  onlogout = this.isLogOut$.asObservable();
  userId: any;

  constructor(
    private httpService : HttpServiceService,
    private dataService: DataService,
    private tokenService: TokenService
    ) {
   }

  //  getUerInfo<Response>(url){
  //    return this.http.get<Response>(this.baseUrl + url, this.options);
  //  }
 
  public createUser(userData){
    // return this.http.post<Response>(this.baseUrl + 'signup', userData, this.options).subscribe(loggedInData=>{
    // });
    this.httpService.post('signup', userData).subscribe((signupData)=>{
        console.log(signupData);
        
    })
  }

  loginWithJwt(loginData: object){
    return this.httpService.post<Response>('login', loginData).subscribe(loggedInData=>{
      if(!loggedInData) return;
      this.tokenService.setRefrshToken(loggedInData["accessToken"]);
      this.isLoggedIn.next(loggedInData['user'].id);
      this.dataService.getPersonalInformation();
      this.dataService.getWorkExperience();
      this.dataService.getSkil();
      this.dataService.getEducation();
      this.dataService.getReference();
    });
  }

  

  // isLogedIn(){
  //   // return this.getToken();
  // }

  // setToken(token){
  //   localStorage.setItem('access_token', token['accessToken']);
  //   localStorage.setItem('referencehToken', token['referencehToken']);
  // }

  // getToken(){
  //   return localStorage.getItem('access_token');
  // }
  
// refresh token
  refreshToken(){
    // let refreshTokenBody = this.getRefrshToken();
    const userObject = this.tokenService.decodeAccessToken(); // get token from local storage

    // this.httpService.post(`refresToken`, refreshTokenBody).subscribe( loggedInData =>{
    this.httpService.post(`refresToken`, userObject).subscribe( loggedInData =>{
      this.isLoggedIn.next(loggedInData['user'].id);
      console.log(loggedInData['user']);

      // if(Object.keys(this.userData).length === 0 ){
      if(!this.userData){
        this.dataService.getPersonalInformation();
        this.dataService.getWorkExperience();
        this.dataService.getSkil();
        this.dataService.getEducation();
        this.dataService.getReference();
      }

    }, (err)=>{
      this.logout();
    })
  }

  


  isUserLoggedIn(){
    const token = this.tokenService.getRefrshToken(); // get token from local storage
    if(token){
      const payload = atob(token.split('.')[1]); // decode payload of token
      const parsedPayload = JSON.parse(payload); // convert payload into an Object
      console.log(parsedPayload.exp > Date.now() / 1000);
      return parsedPayload.exp > Date.now() / 1000;
    }else{
      return false;
    }    
  }

 

  logout(){
    this.tokenService.removeRefrshToken();
    this.httpService.get(`logout`).subscribe(data=>{
      // location.reload();
      this.isLogOut$.next(null);
      this.dataService.userData = {};
    },err=>{
      
    })
  }



}
