import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class TokenService {

  constructor() { }


  setRefrshToken(refrshToken: string){
    return localStorage.setItem('accessToken', refrshToken);
  }

  getRefrshToken(){
    return localStorage.getItem('accessToken');
  }

  removeRefrshToken(){
    localStorage.removeItem('accessToken');
    localStorage.removeItem('referencehToken');
  }

  decodeAccessToken(){
    const token = this.getRefrshToken(); // get token from local storage
    let payload = null;
    if(token){
      
      payload = JSON.parse(atob(token.split('.')[1])) ; // decode payload of token
    } 
    return payload;
  }




}
