import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';
import { HttpServiceService } from './http-service.service';
import { CvDataSetInterface } from './interfaces/userDataSet.interface';
import { AuthService } from './auth/auth.service';
import { TokenService } from './auth/token.service';


@Injectable({
  providedIn: 'root'
})
export class DataService {


  contactMe$ = new Subject();
  oncontactMe = this.contactMe$.asObservable();
  skil$ = new Subject();
  onSkil = this.skil$.asObservable();

  getSkil$ = new Subject();
  onGetSkil = this.getSkil$.asObservable();
  postWorkExperience$ = new Subject();
  onPostWorkExperience = this.postWorkExperience$.asObservable();
  getWorkExperience$ = new Subject();
  ongetWorkExperience = this.getWorkExperience$.asObservable();
  postEducation$ = new Subject();
  onEducationPost = this.postEducation$.asObservable();
  getEducation$ = new Subject();
  onGetEducation = this.getEducation$.asObservable();

  getReference$ = new Subject();
  onGetReference = this.getReference$.asObservable();

  editCv$ = new Subject();
  onEditCv = this.editCv$.asObservable();
  downloadBtn$ = new Subject();
  onDownloadBtnSub = this.downloadBtn$.asObservable();

  isCvOnEditMode: boolean = false;

  cvDataSetForLoggedInUser: CvDataSetInterface ; // logged in user all data will be store inside this object
  getCVDataSub = new Subject();
  onGetCBData$ = this.getCVDataSub.asObservable();

  userData: any = {};

  constructor(
    private http: HttpServiceService,
    private tokenService: TokenService
  ) { 


  


  }


  prepearCvData(){
    
  }

  onClickEidtCv(){
    this.isCvOnEditMode = !this.isCvOnEditMode;
    this.editCv$.next(this.isCvOnEditMode);
  }

onClickDownloadBtn(){
  this.downloadBtn$.next();
}


  getPersonalInformation(){
    const token = this.tokenService.decodeAccessToken();
    this.http.get('personalInfo?userid='+token["id"]).subscribe((data)=>{
      if(!data) return;
      this.contactMe$.next(data);
      this.userData = Object.assign(this.userData, {"personalInfoData": data});
      

    },err=>{
      console.log(err);      
    });
  }

postPersonalInformation(contactMeInfo){
  this.http.post('personalInfo', contactMeInfo).subscribe(data=>{
    if(!data) return;
    this.contactMe$.next(data);
    this.getPersonalInformation();
  }) 
}


postSkil(skilObj){
  this.http.post('skil', skilObj).subscribe(data=>{
    if(!data) return;
    this.skil$.next(data);
    this.getSkil();
  })
}

getSkil(){
  const token = this.tokenService.decodeAccessToken();
  this.http.get('skil?userid='+token["id"]).subscribe(data=>{
    if(!data) return;
    this.getSkil$.next(data);
    this.userData = Object.assign(this.userData, {"skills": data['data'].skills});
  })
}



postWorkExperience(skilObj){
  this.http.post('workingexperience', skilObj).subscribe(data=>{
    if(!data) return;
    this.postWorkExperience$.next(data);
    this.getWorkExperience();
  })
}

getWorkExperience(){
  const token = this.tokenService.decodeAccessToken();
  this.http.get('workingexperience?userid='+token["id"]).subscribe(data=>{
    this.getWorkExperience$.next(data); 
    this.userData = Object.assign(this.userData, {"experiences": data['data'].experience});

  });
}


postEducation(eduObj){
  this.http.post('educations', eduObj).subscribe(data=>{
    if(!data) return;
    this.postEducation$.next(data);
    this.getEducation();
  })
}

getEducation(){
  const token = this.tokenService.decodeAccessToken();
  this.http.get('educations?userid='+token["id"]).subscribe(data=>{
    this.getEducation$.next(data);  
    this.userData = Object.assign(this.userData, {"educations": data['data'].educations});

  });
}

// reference

postReference(refObj){
  const token = this.tokenService.decodeAccessToken();
  refObj.userId = token["id"];
  this.http.post('references', refObj).subscribe(data=>{
    if(!data) return;
    // this.postReference$.next(data);
    this.getReference();
  })
}


public getReference(): void {
  const token = this.tokenService.decodeAccessToken();
  this.http.get('references?userid='+token["id"]).subscribe((data)=>{
    this.getReference$.next(data);   
    this.userData = Object.assign(this.userData, {"references": data['data'].references});
  });
}


getCVData(){
  // this.http.get('').subscribe((data: CvDataSetInterface)=>{
  //   this.getCVDataSub.next(data);   
  // this.cvDataSetForLoggedInUser = data;
  // });

}






}
