import { Injectable } from '@angular/core';
import { ImgurHttpService } from './imgur-http.service';
import { Subject } from 'rxjs';
import { DataService } from '../data.service';

@Injectable({
  providedIn: 'root'
})
export class ImgurService {

  constructor(
    private imgurHttpService: ImgurHttpService,
    private dataService: DataService
  ) { }


  imgurPostSuccessSub = new Subject();
  onImgurPostSuccess$ = this.imgurPostSuccessSub.asObservable();

  uploadImageToImgur(imgurData){
  
    this.imgurHttpService.postImgur(imgurData).subscribe(data=>{
      // this.dataService.postPersonalInformation()
      this.imgurPostSuccessSub.next(data);
    }) 
  }

  



}
